.popular{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 26px;
    height: 95vh;
    margin-top: 20px;
}
.popular h1{
    color: #171717;
    font-size: 50px;
    font-weight: 600;

}
.popular hr{
    width: 200px;
    height: 6px;
    border-radius: 10px;
    background-color: #252525;

}
.popular-item{
    margin-top: 50px;
    display: flex;
    gap: 20px;
    
}